// src/pages/machines.js
import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import * as styles from "../components/index.module.css"

// 假设这是您的机台数据
const machines = [
  { id: 1, name: "机台 A", description: "机台 A 的描述...", imageUrl: "/path/to/image-a.jpg" },
  { id: 2, name: "机台 B", description: "机台 B 的描述...", imageUrl: "/path/to/image-b.jpg" },
  // 添加更多机台...
];

const MachinesPage = () => {
  return (
    <Layout>
      <div className="container mx-auto py-8">
        <h1 className="text-4xl font-bold text-center mb-8">工廠機台介紹</h1>
        <div className="flex flex-wrap -mx-4">
          {machines.map(machine => (
            <div key={machine.id} className="w-full md:w-1/2 lg:w-1/2 px-4 mb-8">
              <div className="bg-white rounded-lg overflow-hidden shadow-lg">
                <img src={machine.imageUrl} alt={machine.name} className="w-full h-64 object-cover" />
                  <div className="p-4">
                  <h2 className="text-xl font-semibold mb-2">{machine.name}</h2>
                    <p>{machine.description}</p>
              </div>
            </div>
        </div>
        ))}
      </div>
    </div>
</Layout>
)
}

export default MachinesPage
